'use strict';
const angular = require('angular');

const uiRouter = require('angular-ui-router');

import routes from './ar.routes';

export class ArComponent {
  /*@ngInject*/
  constructor() {
    this.message = 'Hello';
  }
}

export default angular.module('efwaipeeApp.ar', [uiRouter])
  .config(routes)
  .component('ar', {
    template: require('./ar.html'),
    controller: ArComponent,
    controllerAs: 'arCtrl'
  })
  .name;



