	module.exports = "<div class=\"container\">\r\n  <div class=\"row\">\r\n        <div class=\"col s12 m12\">\r\n          <div class=\"card blue darken-1\">\r\n            <div class=\"card-content white-text\">\r\n              <span class=\"card-title\">About Us</span>\r\n              <p>Welcome to Centre for Graduate Studies at Universiti Teknologi PETRONAS. Our mission is to guide and support students throughout their university experience from admission to graduation. If you are a student, our goal is simple: to promote your success. \r\n \r\nThrough the Office of Admissions, we hope to facilitate your smooth entry into the University. Through the One-Stop Shop and the Office of all Aids, we seek to get you registered for the course that you need and assist you in applying for financial aid / sponsorship. \r\n \r\nThe Graduation Office helps to facilitate your transition from a student to a degree-holding graduate and researcher while you are at Universiti Teknologi PETRONAS, and in the years beyond graduation</p>\r\n            </div>\r\n            <div class=\"card-action\">\r\n              <a href=\"https://www.utp.edu.my/Academic/cgs/SitePages/Home.aspx\">Home</a>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </div>\r\n      <div class=\"card-panel blue darken-1 text-center flow-text\">\r\n          <a class=\"white-text\" ng-href=\"/ar.html\" target=\"_self\">AR</a>\r\n        </div>\r\n    <div class=\"row\">\r\n      <a href=\"/thesis\">\r\n      <div class=\"col s12 m4\">\r\n        <div class=\"card-panel blue darken-1 text-center flow-text\">\r\n          <span class=\"white-text\">CGS</span>\r\n        </div>\r\n      </div>\r\n      </a>\r\n      <a href=\"/qrrecognizer\">\r\n      <div class=\"col s12 m4\">\r\n        <div class=\"card-panel blue darken-1 text-center flow-text\">\r\n          <span class=\"white-text\">Examiner</span>\r\n        </div>\r\n      </div>\r\n      </a>\r\n      <a href=\"/dashboard\">\r\n      <div class=\"col s12 m4\">\r\n        <div class=\"card-panel blue darken-1 text-center flow-text\">\r\n          <span class=\"white-text\">Student</span>\r\n        </div>\r\n      </div>\r\n      </a>\r\n    </div>\r\n</div>\r\n"

